.footer {
    margin-top: 200px;
    padding-bottom: 120px;
    &-container {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
    }
    &-left {
        grid-column: 1/3;
        display: grid;
    }
    &-logo {
        font-family: "Chronicle Display";
        font-size: 32px;
        line-height: 38px;
        letter-spacing: 0.01em;
        text-transform: capitalize;
        color: #ffffff;
        font-weight: bold;
    }
    &-text {
        font-weight: bold;
        font-size: 18px;
        line-height: 32px;
        color: #ffffff;
        max-width: 303px;
    }
    &-a {
        font-weight: 500;
        font-size: 18px;
        line-height: 32px;
        color: #ffffff;
        opacity: 0.5;
        align-self: flex-end;
    }
    &-orange {
        font-weight: bold;
        font-size: 24px;
        line-height: 32px;
        color: #fbd784;
    }
    &-list {
        list-style: none;
        padding: 0;
        li {
            font-weight: 500;
            font-size: 18px;
            line-height: 32px;
            margin: 16px 0;
            color: #ffffff;
        }
    }
    &-center,
    &-right {
        justify-self: end;
    }
}
