// Подключение своих SCSS файлов
@import "fonts";
@import "header";
body {
    font-family: "Gilroy";
}
a,
button {
    color: #fff;
    text-decoration: none;
    border: none;
    cursor: pointer;
}
html {
    scroll-behavior: smooth;
}
.wrapper {
    max-width: 1920px;
    margin: -30px auto 0;
    // background: #0b1d26;
    background: linear-gradient(180deg, rgba(11, 29, 38, 0.5) 0%, #0b1d26 61.38%);
    min-height: 100%;
}
.header {
}
main {
    // background: url("../img/2.png");
    // height: 1000px;

    position: relative;
}
.main_img {
    // margin-top: 690px;
    position: absolute;
    top: 690px;
}
.main_img_1 {
    position: absolute;
    top: -300px;
    z-index: -1;
}
.main_image_block {
    position: relative;
    width: 100%;
    > img {
        width: 100%;
    }
}
.main_img_2 {
    top: 200px;
    // width: 100%;
    // height: 300px;
    position: absolute;
}
.main_content {
    margin-top: 100px;
    &-main {
        display: flex;
        justify-content: space-between;
        grid-template-columns: repeat(3, 1fr);
        z-index: 11;
    }
}
.main-left {
    display: grid;
    justify-items: center;
    margin-top: 150px;
}

.rotate {
    transform: rotate(90deg);
    width: max-content;
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    color: #ffffff;
    margin: 0;
    margin: 0px 0 0px 50px;
    display: flex;
    align-items: center;
}
.left-soc {
    display: grid;
    margin: 30px 0 0 40px;
    height: max-content;
    a {
        margin: 10px 0;
    }
}
.title-orange {
    font-weight: 800;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 6px;
    text-transform: uppercase;
    color: #fbd784;
    display: flex;
    align-items: center;
}
.line {
    &::before {
        content: "";
        display: inline-block;
        width: 72px;
        height: 2px;
        margin-right: 32px;
    }
    &-orange {
        &::before {
            background: #fbd784;
        }
    }
}
.main-center {
    z-index: 1;
}

.main_title {
    font-family: "Chronicle Display";
    font-style: normal;
    font-weight: 600;
    font-size: 88px;
    line-height: 100px;
    text-transform: capitalize;
    color: #ffffff;
    max-width: 950px;
    margin-bottom: 10px;
    z-index: 11;
}
.text {
    color: #fff;
    font-weight: bold;
    display: flex;
    align-items: center;
}
.arrow {
    position: relative;
    display: flex;
    align-items: center;
    width: max-content;
    &::after {
        width: 16px;
        margin-left: 10px;
        content: "";
        display: inline-block;
        height: 1px;
        background: #fff;
    }
    &::before {
        content: "";
        display: inline-block;
        position: absolute;
        width: 5px;
        height: 5px;
        border: 1px solid #fff;
        border-bottom: transparent;
        border-right: transparent;
        transform: rotate(135deg);
        top: 36%;
        left: 95%;
    }
    &-rotate {
        &::after {
            transform: rotate(90deg);
        }
        &::before {
            transform: rotate(225deg);
            left: 90%;
            top: 70%;
        }
    }
    &-orange {
        &::after {
            background: #fbd784;
        }
        &::before {
            border-top-color: #fbd784;
            border-left-color: #fbd784;
        }
    }
}
main {
    position: relative;
}
.main_blocks {
    margin: 55% auto 0;
    z-index: 10;
    position: relative;
}
.container {
    max-width: 1462px;
    margin-left: auto;
    margin-right: auto;
}
.main_block {
    margin: 200px auto 0;
    max-width: 1462px;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    &-left {
        max-width: 632px;
        margin: auto;
        position: relative;
    }
}

.order {
    &-2 {
        order: 1;
    }
}
.b_num {
    font-style: normal;
    font-weight: bold;
    font-size: 240px;
    line-height: 240px;
    color: #ffffff;
    opacity: 0.1;
    position: absolute;
    left: -15%;
    // top: -20px;
    top: -60%;
}
.main_block {
    &-title {
        font-family: "Chronicle Display";
        font-weight: 600;
        font-size: 64px;
        line-height: 77px;
        margin: 30px 0;
        color: #ffffff;
    }
    &-text {
        font-weight: bold;
        font-size: 18px;
        line-height: 32px;
        color: #ffffff;
    }
}
.text-orange {
    font-size: 18px;
    line-height: 22px;
    color: #fbd784;
}
.main-right {
    position: relative;
}
.pagination {
    position: fixed;
    top: 250px;
    z-index: 11;
    display: grid;
    margin-left: -100px;
    button::after {
        content: "";
        width: 3px;
        height: 60px;
        background: #fff;
        display: block;
        position: absolute;
        right: -20px;
        top: -15px;
        opacity: 0.5;
    }
    button {
        background: none;
        color: #fff;
        font-size: 18px;
        line-height: 22px;
        margin: 15px 0;
        position: relative;
    }
}
.button-active::after {
    opacity: 1 !important;
}
@import "footer";
@import "adaptive";
