@media all and (min-width: 420px) and (max-width: 800px) {
}
@media all and (max-width: 800px) {
    .acc_logo {
        display: none;
    }
    .burger_menu {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        justify-content: center;
        width: 100%;
        li {
            text-align: center;
            margin: 30px 0;
            a {
                font-size: 25px;
            }
        }
    }
    .burger {
        &-click {
            position: relative;
            &::before {
                content: "";
                display: block;
                width: 40px;
                height: 2px;
                background: #000;
                position: absolute;
                top: -10px;
                transition: all 0.5s ease-in-out;
            }
            &::after {
                content: "";
                display: block;
                width: 40px;
                height: 10px;
                border-bottom: 2px solid #000;
                border-top: 2px solid #000;
                transition: all 0.5s ease-in-out;
            }
        }
        &_menu {
            margin: 0 auto;
            max-height: 0;
            overflow: hidden;
            transition: all 1s ease-in-out;
        }
    }
    .burger-active {
        z-index: 10;
        background: rgba(11, 29, 38, 1);
        width: 100%;
        height: 100%;
        margin-top: -30px;
        .header-container {
            padding-top: 50px;
            margin-bottom: 30px;
        }
        .burger_menu {
            max-height: 1000px;
            z-index: 10;
            li {
                margin: 15px 0;
            }
            > li:last-child {
                margin-bottom: 150%;
            }
        }
        .burger-click {
            &::before {
                transform: rotate(45deg);
                top: 0px;
                left: -2px;
                background: #fff;
            }
            &::after {
                transform: rotate(-45deg);
                border-top-color: #fff;
                border-bottom: transparent;
            }
        }
    }
    .header {
        &-container {
            margin-top: 30px;
            grid-template-columns: repeat(2, 1fr) !important;
        }
        &-left {
            width: max-content;
            margin-left: 50px;
        }
    }
    .wrapper {
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }
}
@media all and (max-width: 700px) {
    .main_image_block {
        .main_img {
            top: 80px;
        }
        .main_img_2 {
            top: -100px;
        }
    }
    .pagination {
        margin-left: 10%;
    }
}
@media all and (max-width: 500px) {
    .pagination {
        margin-left: 5%;
        top: 150px;
    }
    .header {
        padding: 0px 0 0 0;
    }
    .main_image_block {
        .main_img {
            top: 80px;
        }
        .main_img_2 {
            top: -50px;
        }
        .main_img_1 {
            top: -200px;
        }
    }
    .b_num {
        font-size: 150px;
        top: -40%;
    }
    .burger-click {
        margin-right: 20px;
    }
    .footer {
        &-container {
            grid-template-columns: repeat(1, 1fr);
        }
        &-left {
            grid-column: 1/2;
            margin-bottom: 50px;
        }
        &-center,
        &-right {
        }
    }
}
@media all and (max-width: 350px) {
    .b_num {
        top: -30%;
    }
    .main_block {
        margin: 100px auto 0;
        &-title {
            font-size: 44px;
        }
        &-text {
            font-size: 14px;
        }
    }
    .main_blocks {
        margin-top: 30%;
    }
    .main-title_text {
        font-size: 10px !important;
    }
    .line::before {
        margin-right: 15px;
    }
    .rotate {
        font-size: 14px;
    }
}
@media all and (max-width: 350px) {
    .main_image_block {
        .main_img {
            top: 30px;
        }
        .main_img_2 {
            top: -80px;
        }
        .main_img_1 {
            top: -200px;
        }
    }
}
@media all and (max-width: 850px) {
    .main-center {
        margin-top: -50px;
        z-index: 10;
    }
    .main-left {
        margin-top: 50px;
        height: max-content;
    }
    .main_title {
        font-size: 35px;
        line-height: 50px;
    }
    .header-container {
        padding-top: 10px;
    }
}
@media all and (max-width: 670px) {
    .main-center {
        margin-top: -50px;
        z-index: 10;
        width: 50%;
        margin-left: 0;
        margin-right: 0;
    }
    .main-left {
        margin-top: 50px;
        height: max-content;
    }
    .main_title {
        font-size: 35px;
        line-height: 50px;
    }
    .none {
        display: none;
    }
    .main_content-main {
        justify-content: start;
    }
    .main-left {
        z-index: 10;
    }
}
@media all and (max-width: 700px) {
    .b_num {
        left: 0;
    }
    .main_block {
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        img {
            width: 100%;
        }
    }
    .order-2 {
        order: 0;
    }
}
