.header {
	z-index: 1;
	&-left {
	}
	&-center {
		display: flex;
		justify-content: space-between;
		padding: 0;
		width: 350px;
		list-style: none;
		justify-self: center;
		a {
			font-size: 18px;
		}
	}
	&-right {
		display: flex;
		justify-self: end;
		margin-right: 50px;
		.acc {
			margin-left: 20px;
			align-items: center;
		}
	}
	&-container {
		z-index: 1;
		max-width: 1760px;
		margin: 0px auto 60px;
		padding-top: 60px;
		box-sizing: border-box;
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		align-items: center;
		font-weight: 700;
		justify-content: space-between;
		* {
			z-index: 1;
		}
	}
}
.burger-click {
	z-index: 10;
}
